.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1; /* Ensure it's behind other content */
  }
  
  .background-video {
    object-fit: cover; /* Make sure the video covers the screen */
    width: 100%;
    height: 100%;
  }
  